"use client"

import Link from "next/link"
import { useState, useRef, useEffect } from "react"
import { Heart, ListPlus, MoreVertical } from "lucide-react"

type Track = {
  id: number
  title: string
  artist: string
  album?: string
  url: string
  createdAt: string
  cover?: string
  genre?: string
  year?: number
}

export default function TrackItem({
  track,
  onDelete,
  onQueue,
  onPlay,
}: {
  track: Track,
  onDelete?: (id: number) => void,
  onQueue?: (id: number) => void,
  onPlay?: (track: Track) => void,
}) {
  const [menu, setMenu] = useState<{ x: number, y: number } | null>(null)
  const [isFavorite, setIsFavorite] = useState(false)
  const menuRef = useRef<HTMLDivElement>(null)

  const [playlists, setPlaylists] = useState<any[]>([])
  const [showPlaylists, setShowPlaylists] = useState(false)

  useEffect(() => {
    checkIfFavorite()
  }, [track.id])

  // ... (checkIfFavorite, toggleFavorite, recordListen, handlePlay kept as logic but not duplicated in code block if I can avoid retyping all)
  // I will replace the whole component to be cleaner.

  const checkIfFavorite = async () => {
    try {
      const res = await fetch("/api/favorites")
      const data = await res.json()
      setIsFavorite(data.some((f: any) => f.trackId === track.id))
    } catch (error) {
      console.error("Erreur v√©rification favori:", error)
    }
  }

  const toggleFavorite = async () => {
    try {
      if (isFavorite) {
        const res = await fetch("/api/favorites")
        const data = await res.json()
        const fav = data.find((f: any) => f.trackId === track.id)
        if (fav) {
          await fetch(`/api/favorites?id=${fav.id}`, { method: "DELETE" })
          setIsFavorite(false)
        }
      } else {
        await fetch("/api/favorites", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ type: "track", trackId: track.id }),
        })
        setIsFavorite(true)
      }
    } catch (error) {
      console.error("Erreur toggle favori:", error)
    }
  }

  const recordListen = async () => {
    try {
      await fetch("/api/history", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ trackId: track.id }),
      })
    } catch (error) {
      console.error("Erreur enregistrement √©coute:", error)
    }
  }

  const fetchPlaylists = async () => {
    try {
      const res = await fetch("/api/playlists")
      const data = await res.json()
      setPlaylists(Array.isArray(data) ? data : [])
      setShowPlaylists(true)
    } catch (error) {
      console.error("Erreur chargement playlists:", error)
    }
  }

  const addToPlaylist = async (playlistId: number) => {
    try {
      await fetch(`/api/playlists/${playlistId}/tracks`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ trackId: track.id }),
      })
      alert("Ajout√© √† la playlist !")
      setMenu(null)
      setShowPlaylists(false)
    } catch (error) {
      console.error("Erreur ajout playlist:", error)
      alert("Erreur lors de l'ajout")
    }
  }

  // Fermer le menu si on clique ailleurs
  useEffect(() => {
    if (!menu) return
    const close = (e: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(e.target as Node)) {
        setMenu(null)
        setShowPlaylists(false)
      }
    }
    document.addEventListener("mousedown", close)
    return () => document.removeEventListener("mousedown", close)
  }, [menu])

  return (
    <div
      className="relative group bg-gray-50 dark:bg-gray-800 p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 transition"
      onContextMenu={e => {
        e.preventDefault()
        setMenu({ x: e.clientX, y: e.clientY })
        setShowPlaylists(false)
      }}
    >
      <div className="flex gap-4 items-center">
        {track.cover ? (
          <img
            src={track.cover}
            alt="cover"
            className="w-16 h-16 object-cover rounded cursor-pointer hover:scale-105 transition shadow-sm"
            onClick={e => {
              e.stopPropagation()
              onPlay?.(track)
            }}
            title="√âcouter"
          />
        ) : (
          <div
            className="w-16 h-16 bg-gray-200 dark:bg-gray-600 rounded flex items-center justify-center text-gray-400 dark:text-gray-300 cursor-pointer"
            onClick={e => {
              e.stopPropagation()
              onPlay?.(track)
            }}
            title="√âcouter"
          >
            üéµ
          </div>
        )}
        <div className="flex-1 min-w-0">
          <Link
            href={`/musique/${track.id}`}
            className="block cursor-pointer hover:underline truncate"
            title={track.title}
          >
            <div className="font-semibold text-base truncate">{track.title}</div>
            <div className="text-sm text-gray-500 dark:text-gray-400 truncate">
              {track.artist}
              {track.album && <> ‚Äî <span className="italic">{track.album}</span></>}
            </div>
          </Link>
        </div>
        
        <button
          onClick={toggleFavorite}
          className={`p-2 rounded-full transition ${isFavorite ? "text-red-500" : "text-gray-400 hover:text-red-500"}`}
          title={isFavorite ? "Retirer des favoris" : "Ajouter aux favoris"}
        >
          <Heart size={20} fill={isFavorite ? "currentColor" : "none"} />
        </button>

        <button
          className="p-2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-200"
          onClick={(e) => {
            e.stopPropagation()
            setMenu({ x: e.clientX, y: e.clientY })
            setShowPlaylists(false)
          }}
        >
          <MoreVertical size={20} />
        </button>
      </div>

      {menu && (
        <div
          ref={menuRef}
          style={{ position: "fixed", top: menu.y, left: menu.x, zIndex: 100 }}
          className="bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-700 rounded-lg shadow-xl flex flex-col min-w-[200px] overflow-hidden py-1"
        >
          <div className="px-3 py-2 text-xs font-semibold text-gray-500 uppercase tracking-wider border-b border-gray-100 dark:border-gray-800 mb-1">
            Actions
          </div>
          <button
             className="px-4 py-2 text-left hover:bg-gray-100 dark:hover:bg-gray-800 flex items-center gap-2"
             onClick={() => { setMenu(null); onPlay?.(track) }}
          >
            <span>‚ñ∂Ô∏è</span> √âcouter
          </button>
          <button
            className="px-4 py-2 text-left hover:bg-gray-100 dark:hover:bg-gray-800 flex items-center gap-2"
            onClick={() => { setMenu(null); onQueue?.(track.id) }}
          >
            <span>üì•</span> Ajouter √† la file
          </button>

          {!showPlaylists ? (
            <button
              className="px-4 py-2 text-left hover:bg-gray-100 dark:hover:bg-gray-800 flex items-center gap-2"
              onClick={(e) => {
                 e.stopPropagation(); 
                 fetchPlaylists();
              }}
            >
              <span>‚ûï</span> Ajouter √† une playlist...
            </button>
          ) : (
            <div className="border-t border-gray-100 dark:border-gray-800 mt-1">
              <div className="bg-gray-50 dark:bg-gray-800/50 px-3 py-1 text-xs text-gray-500">
                Choisir une playlist :
              </div>
              <div className="max-h-40 overflow-y-auto">
                {playlists.length === 0 ? (
                  <div className="px-4 py-2 text-sm text-gray-400 italic">Aucune playlist</div>
                ) : (
                  playlists.map(p => (
                    <button
                      key={p.id}
                      className="w-full px-4 py-2 text-left text-sm hover:bg-blue-50 dark:hover:bg-blue-900/30 truncate"
                      onClick={() => addToPlaylist(p.id)}
                    >
                      {p.name}
                    </button>
                  ))
                )}
              </div>
              <button 
                className="w-full text-center py-1 text-xs text-blue-500 hover:underline"
                onClick={(e) => { e.stopPropagation(); setShowPlaylists(false); }}
              >
                Retour
              </button>
            </div>
          )}

          {onDelete && (
            <>
              <div className="h-px bg-gray-100 dark:bg-gray-800 my-1"></div>
              <button
                className="px-4 py-2 text-left text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 flex items-center gap-2"
                onClick={() => { setMenu(null); onDelete(track.id) }}
              >
                <span>üóëÔ∏è</span> Supprimer
              </button>
            </>
          )}
        </div>
      )}
    </div>
  )
}
